.productDetailsPage {
  display: flex;
  flex-direction: column;
  gap: 60px;
  inline-size: 100%;
  position: relative;
}

.productDetailsPage .info {
  flex-basis: 50%;
  flex-grow: 1;
  font-size: 24px;
  font-weight: 800;
  letter-spacing: 1.69px;
}

.productDetailsPage .price {
  font-size: 30px;
  font-weight: 600;
  letter-spacing: 1.69px;
  margin-block-start: 20px;
}

.productDetailsPage .SubmitButton {
  margin-block-start: 30px;
}

.productDetailsPage .description {
  font-size: 21px;
  font-weight: 600;
  letter-spacing: 1.69px;
}

.productDetailsPage .success {
  color: var(--text-accent);
  font-size: 16px;
  margin-block-start: 6px;
}

@media (min-width: 800px) {
  .productDetailsPage {
    flex-direction: row;
    gap: 60px;
  }

  .productDetailsPage .info {
    max-inline-size: 45%;
  }
}
